:root {
  --category-product-list-load-button-background: var(--secondary-base-color); }

@media (min-width: 811px) {
  .RecentlyViewedWidget {
    grid-column: 2;
    min-height: auto;
    padding-bottom: 2rem; } }

.RecentlyViewedWidget-Page {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-auto-rows: max-content;
  grid-column-gap: 1rem; }
  @media (min-width: 811px) and (max-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    .RecentlyViewedWidget-Page {
      grid-template-columns: repeat(4, 1fr); } }
  @media (max-width: 810px) {
    .RecentlyViewedWidget-Page {
      border: 0;
      grid-template-columns: 1fr 1fr; } }
  @media (min-width: 811px) {
    .RecentlyViewedWidget-Page {
      grid-column: 2; } }

.RecentlyViewedWidget-ProductsMissing {
  padding: 1rem;
  text-align: center; }

.RecentlyViewedWidget_isLoading {
  animation: appear 100ms 200ms ease-in forwards;
  opacity: 0; }

.RecentlyViewedWidget-More {
  min-height: 3rem; }

.RecentlyViewedWidget-LoadButton {
  background-color: var(--category-product-list-load-button-background);
  cursor: pointer;
  font-size: 13pt;
  margin-bottom: 1rem;
  padding: 1em 0;
  text-align: center; }
